html {
  font-size: $base-font-size;
}

html,
body {
  margin: 0;
}

body {
  overflow-x: hidden;

  font-size: 1rem;
  font-family: $font-family-primary;
  line-height: 1.4;
  font-weight: 300;

  background-color: #8c0032;

  @include mq(md) {
    background-color: $bg-color;
  }
}

h1,
h2,
h3 {
  font-weight: 400;
}

h1 {
  font-size: 1.5rem;
}

h2 {
  font-size: 1.2rem;
}

input,
textarea,
select,
button,
label {
  font-size: 1rem;
  font-family: $font-family-primary;
}

input,
textarea {
  font-weight: 300;
}

a {
  color: white;
}

a:hover {
  color: white;
  text-decoration: none;
}

img {
  max-width: 100%;
}