.bottom-items-slider {
  padding: 12px 30px;
  width: 98%;
  @include mx(auto);
  margin-top: 30px;
  margin-bottom: 30px;
  background-image: linear-gradient(to top, #8c0032, #8c0032 35%, #c2185b 35%);
  border-bottom: 1px solid #96043a;

  @include mq(lg) {
    padding: 45px 100px;
    margin-top: 90px;
    margin-bottom: 90px;
  }

  .item-box {
    font-size: 12px;
    font-weight: 400;
    background-position: center;
    background-size: cover;
    width: 200px;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .item-box__title {
    height: 36px;
    width: 100%;
    text-align: center;
    text-transform: uppercase;
    background-color: rgba(255, 255, 255, .7);
    display: flex;
    flex-direction: column;
    justify-content: center;
    a {
      color: #a3395d;
    }
  }

  .girl {
    .item-box__info {
      align-items: center;
      justify-content: space-between;
      padding-left: 25px;
      padding-right: 25px;
    }
  }

  .service {
    .item-box__info {
      align-items: center;
      justify-content: center;
      text-align: center;
    }
  }

  .story {

    .item-box__info {
      @include px(15px);
      padding-top: 5px;
      padding-bottom: 5px;
      overflow: hidden;
      text-align: center;
      align-items: center;
  
      span {
        height: 17px;
        display: inline-block;
        overflow: hidden;
      }
      
      a {
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }

  .item-box__info {
    background-color: rgba(247, 107, 150, 0.7);
    height: 36px;
    // max-height: 90px;
    width: 100%;
    display: flex;

    a {
      display: block;
      max-height: 90px;
    }
  }

  .slick-next,
  .slick-prev {

  }
  .slick-next,
  .slick-prev {
    width: 45px;
    height: 45px;
    background-color: #8c0032;
  }
  .slick-next {
    right: -45px;

    @include mq(lg) {
      right: -128px;
    }
  }
  .slick-prev {
    left: -45px;

    @include mq(lg) {
      left: -128px;
    }
  }
  .slick-next:before,
  .slick-prev:before {
    font-family: 'FontAwesome';
    color: #c2185b;
  }
  .slick-next:before {
    content: '\f061';
  }
  .slick-prev:before {
    content: '\f060';
  }
}


